import { Dispatch, SetStateAction } from "react"
import { Box, Fab } from "@rp/react/web/mui/core"
import { ChatBubbleOutline } from "@rp/react/web/mui/icons"

type Props = {
  isChatCollapsed: boolean
  setIsChatCollapsed: Dispatch<SetStateAction<boolean>>
}
export const WatchlistChatCollapsedButton = ({ isChatCollapsed, setIsChatCollapsed }: Props) => {
  if (!isChatCollapsed) return null
  return (
    <Fab
      color="primary"
      variant="extended"
      sx={{
        position: "fixed",
        fontSize: 16,
        bottom: 20,
        right: 20,
        textTransform: "none",
      }}
      onClick={() => setIsChatCollapsed(prev => !prev)}
    >
      <ChatBubbleOutline />
      <Box sx={{ px: 1 }}>Chat</Box>
    </Fab>
  )
}
